<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button class="addrole" type="primary" @click="addRole"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="rolesList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template v-slot="scope">
            <!--  -->
            <el-row :class="['bdbottom', index1 == 0 ? 'bdtop' : '', 'vertical-center']"
                    v-for="(item1, index1) in scope.row.children" :key="item1.id">
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row, item1.id, {}, {})">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <el-col :span="19">
                <el-row :class="[index2 == 0 ? '' : 'bdtop', 'vertical-center']"
                        v-for="(item2, index2) in item1.children" :key="item2.id">
                  <!--  -->
                  <el-col :span="6">
                    <el-tag closable @close="removeRightById(scope.row, item2.id, item1, {})" type="success">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="18">
                    <el-tag type="warning" v-for="item3 in item2.children" :key="item3.id"
                            closable @close="removeRightById(scope.row, item3.id, item2, item1)">
                      {{item3.authName}}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
            <!-- <pre>
              {{scope.row}}
            </pre> -->
          </template>
        </el-table-column>
        
        <el-table-column type="index" align="center"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="200px" align="center">
          <template v-slot="scope">
            <!--  -->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditRoleDialog(scope.row.id)"></el-button>
            <!--  -->
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeRoleById(scope.row.id)"></el-button>
            <!--  -->
            <el-tooltip content="" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini"
                        @click="showAssignPermissionsDialog(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="assignPermissionsDialogVisible"
      width="600px"
      @close="assignPermissionsDialogClosed">
      <!-- () -->
      <el-tree
        :data="rightsList"
        show-checkbox
        node-key="id"
        default-expand-all
        :props="treeProps"
        :default-checked-keys="defaultKeys"
        ref="treeRef">
      </el-tree>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="assignPermissionsDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addRoleDialogVisible"
      width="600px"
      @close="addRoleDialogClosed">
      <!--  -->
      <el-form @submit.native.prevent :model="addRoleForm" :rules="addRoleFormRules" ref="addRoleFormRef" label-width="134px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="addRoleForm.roleName" placeholder="" clearable>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addRoleForm.roleDesc" type="textarea" placeholder=""
                    maxlength="40" resize="none" :autosize="{ minRows: 2, maxRows: 6 }">
          </el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveAddRole"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editRoleDialogVisible"
      width="600px"
      @close="editRoleDialogClosed">
      <!--  -->
      <el-form @submit.native.prevent :model="editRoleForm" :rules="editRoleFormRules" ref="editRoleFormRef" label-width="134px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="editRoleForm.roleName" placeholder="" clearable>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editRoleForm.roleDesc" type="textarea" placeholder=""
                    maxlength="40" resize="none" autosize>
          </el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveEditRole"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      rolesList: [],
      //  
      assignPermissionsDialogVisible: false,
      //  
      rightsList: [],
      //  
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      //  
      defaultKeys: [],
      // ID
      roleId: '',
      //  
      addRoleDialogVisible: false,
      // 
      addRoleForm: {
        roleName: '',
        roleDesc: ''
      },
      //  
      addRoleFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ]
      },
      //  
      editRoleDialogVisible: false,
      //  
      editRoleForm: {},
      //  
      editRoleFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ]
      },
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    // 
    async getRolesList() {
      const {data: res} = await this.$http.get('roles')
      if(res.meta.status !== 200) {
        return this.$message.error({
          message: '!',
          duration: 1000
        })
      }
      this.rolesList = res.data
      // console.log(this.rolesList);
    },
    // id
    async removeRightById(role, rightId, parentRight, grandParentRight) {
      // 
      const confirmRes = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      // console.log(confirmRes);
      if(confirmRes !== 'confirm') {
        return this.$message({
          message: '',
          duration: 1000
        })
      }
      // 
      const {data: res} = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
      if(res.meta.status !== 200) {
        return this.$message.error({
          message: '!',
          duration: 1000
        })
      }
      role.children = res.data
      // console.log(res.data);
      // console.log(role, rightId, parentRight);
      // **
      if(parentRight && parentRight.children && (parentRight.children.length !== 1)) return 
      // // 
      // // console.log(role.id, parentRight.id);
      const {data: parentRes} = await this.$http.delete(`roles/${role.id}/rights/${parentRight.id}`)
      if(!parentRes.data) {
        return 
      }
      role.children = parentRes.data
      // // console.log(parentRes);
      // console.log(grandParentRight);
      if(grandParentRight && grandParentRight.children && (grandParentRight.children.length !== 1)) {
        return 
      }
      const {data: grandParentRes} = await this.$http.delete(`roles/${role.id}/rights/${grandParentRight.id}`)
      // 
      if(grandParentRes.data) {
        return role.children = grandParentRes.data
      }
    },
    // 
    async showAssignPermissionsDialog(role) {
      // IDdata
      this.roleId = role.id
      // 
      const {data: res} = await this.$http.get('rights/tree')
      if(res.meta.status !== 200) {
        return this.$message.error({
          message: res.meta.msg,
          duration: 1000
        })
      }
      // data
      this.rightsList = res.data
      // console.log(this.rightsList);
      // id
      this.getLeafKeys(role, this.defaultKeys)
      // 
      this.assignPermissionsDialogVisible = true
    },
    // id
    getLeafKeys(node, arr) {
      if(!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      });
    },
    // 
    assignPermissionsDialogClosed() {
      this.defaultKeys = []
    },
    // 
    async allotRights() {
      // ID
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      // console.log(keys);
      // 
      const {data: res} = await this.$http.post(`roles/${this.roleId}/rights`, {rids: idStr})
      if(res.meta.status !==200) {
        return this.$message.error({
          message: '',
          duration: 1000
        })
      }
      this.$message.success({
        message: '',
        duration: 1000
      })
      // 
      this.getRolesList()
      this.assignPermissionsDialogVisible = false
    },
    // 
    addRole() {
      this.addRoleDialogVisible = true
    },
    // 
    saveAddRole() {
      this.$refs.addRoleFormRef.validate(async valid => {
        if(!valid) return
        // console.log(valid);
        const {data: res} = await this.$http.post('roles', this.addRoleForm)
        if(res.meta.status !==201) {
          return this.$message.error({
            message: '!',
            duration: 1000
          })
        }
        this.$message.success({
          message: '!',
          duration: 1000
        })
        this.getRolesList()
        this.addRoleDialogVisible = false
      })
    },
    // 
    addRoleDialogClosed() {
      this.$refs.addRoleFormRef.resetFields()
    },
    // 
    async showEditRoleDialog(roleId) {
      // console.log(roleId);
      const {data: res} = await this.$http.get('roles/' + roleId)
      if(res.meta.status !== 200) {
        return this.$message.error({
          message: '!',
          duration: 1000
        })
      }
      // console.log(this.editRoleForm);
      this.editRoleDialogVisible = true
      /* 
        domresetFields()
        clearValidate()
      */
      this.$nextTick(() => {
          this.editRoleForm = res.data
      })
      // this.editRoleForm = res.data
    },
    // ()
    saveEditRole() {
      this.$refs.editRoleFormRef.validate(async valid => {
        if(!valid) return
        const {data: res} = await this.$http.put('roles/' + this.editRoleForm.roleId, {
          roleName: this.editRoleForm.roleName,
          roleDesc: this.editRoleForm.roleDesc
        })
        if(res.meta.status !== 200) {
          return this.$message.error({
            message: '!',
            duration: 1000
          })
        }
        // console.log(res);
        this.editRoleDialogVisible = false
        this.getRolesList()
        this.$message.success({
          message: '!',
          duration: 1000
        })
      })     
    },
    // 
    editRoleDialogClosed() {
      // this.$refs.editRoleFormRef.resetFields()
      // 
      this.$refs.editRoleFormRef.clearValidate()
    },
    // ID
    removeRoleById(roleId) {
      // console.log(roleId);
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const {data: res} = await this.$http.delete('roles/' + roleId)
        if(res.meta.status !== 200) {
          return this.$message.error({
            message: '!',
            duration: 1000
          })
        }
        this.$message.success({
          message: '!',
          duration: 1000
        })
        this.getRolesList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '',
          duration: 1000
        });          
      });
    }
  }
}
</script>

<style lang="less" scoped>
.addrole {
  padding-left: 15px;
  padding-right: 13px;
  font-size: 14px;
  letter-spacing: .8px;
}
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #EBEEF5;
}
.bdbottom {
  border-bottom: 1px solid #EBEEF5;
}
.vertical-center {
  display: flex;
  align-items: center;
}
.el-input {
  width: 350px;
}
.el-textarea {
  width: 350px;
}
</style>